import axios from 'axios'
import Qs from 'qs'
import { shim } from 'promise.prototype.finally'
shim()

const defaultsAxiosOptions = {
  // `url`  URL
  // url: '/user',

  // `method` 
  // method: 'get', //  get

  // `baseURL`  `url`  `url`  URL
  //  `baseURL`  axios  URL
  // baseURL: 'https://some-domain.com/api/',

  // `transformRequest` 
  //  'PUT', 'POST'  'PATCH' 
  //  ArrayBuffer Stream
  // transformRequest: [function (data, headers) {
  //   //  data 
  //   if (data && data.toString() === '[object FormData]') {
  //     return data
  //   }
  //   if (headers['Content-Type'] && headers['Content-Type'].indexOf('application/json') > -1) {
  //     return JSON.stringify(data)
  //   } else if (Array.isArray(data)) {
  //     return JSON.stringify(data)
  //   }
  //   else {
  //     return Qs.stringify(data)
  //   }
  // }],

  // `transformResponse`  then/catch 
  // transformResponse: [function(data) {
  //     //  data 
  //     return data;
  // }],
  transformRequest: [
    data => Qs.stringify(data, { arrayFormat: 'repeat', skipNulls: true })
  ],

  // `headers` 
  // headers: { 'X-Request-Module': window.location.href },
  headers: {
    'Content-Type': 'application/x-www-form-urlencoded'
  },
  // `params`  URL 
  // (plain object) URLSearchParams 
  // params: {
  //     ID: 12345
  // },

  // `paramsSerializer`  `params` 
  // (e.g. https://www.npmjs.com/package/qs, http://api.jquery.com/jquery.param/)
  paramsSerializer: params => {
    return Qs.stringify(params, { arrayFormat: 'repeat', skipNulls: true })
  }

  // `data` 
  //  'PUT', 'POST',  'PATCH'
  //  `transformRequest` 
  // - string, plain object, ArrayBuffer, ArrayBufferView, URLSearchParams
  // - FormData, File, Blob
  // - Node  Stream
  // data: {
  //     firstName: 'Fred'
  // },

  // `timeout` (0 )
  //  `timeout` 
  // timeout: 1000,

  // `withCredentials` 
  // withCredentials: false, // 

  // `adapter` 
  //  promise  ( [response docs](#response-api)).
  // adapter: function(config) {
  //     /* ... */
  // },

  // `auth`  HTTP 
  //  `Authorization`  `headers`  `Authorization`
  // auth: {
  //     username: 'janedoe',
  //     password: 's00pers3cret'
  // },

  // `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
  // responseType: 'json', // 

  // `xsrfCookieName`  xsrf token cookie
  // xsrfCookieName: 'XSRF-TOKEN', // default

  // `xsrfHeaderName`  xsrf token  HTTP 
  // xsrfHeaderName: 'X-XSRF-TOKEN', // 

  // `onUploadProgress` 
  // onUploadProgress: progressEvent=> {
  //     // 
  // },

  // `onDownloadProgress` 
  // onDownloadProgress: progressEvent=> {
  //     // 
  // },

  // `maxContentLength` 
  // maxContentLength: 2000,

  // `validateStatus` HTTP  resolve  reject  promise  `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  reject
  // validateStatus: status=> {
  //     return status >= 200 && status < 300; // 
  // },

  // `maxRedirects`  node.js  follow 
  // 0 follow 
  // maxRedirects: 5, // 

  // `httpAgent`  `httpsAgent`  node.js  http  https 
  // `keepAlive` 
  // httpAgent: new http.Agent({ keepAlive: true }),
  // httpsAgent: new https.Agent({ keepAlive: true }),

  // 'proxy' 
  // `auth`  HTTP 
  //  `Proxy-Authorization`  `header`  `Proxy-Authorization` 
  // proxy: {
  //     host: '127.0.0.1',
  //     port: 9000,
  //     auth:: {
  //         username: 'mikeymike',
  //         password: 'rapunz3l'
  //     }
  // },

  // `cancelToken`  cancel token
  //  Cancellation 
  // cancelToken: new CancelToken(function(cancel) {})
}

let api = {
  install: (Vue, { requestIntercept, responseSuccIntercept, responseErrorIntercept, globalAxiosOptions, apiConfig, hosts, router } = {}) => {
    // http
    axios.interceptors.request.use(config => {
      requestIntercept && requestIntercept(config)
      return config
    }, err => {
      return Promise.reject(err)
    })

    // http
    axios.interceptors.response.use(resp => {
      delete $api.cancelStack[resp.config.name]
      responseSuccIntercept && responseSuccIntercept(resp)
      return resp
    }, err => {
      responseErrorIntercept && responseErrorIntercept(err)
      return Promise.reject(err)
    })

    // 
    const ajax = options => {
      options = Object.assign({}, options, {
        name: options['name'] || Math.random().toString(),
        cancelToken: new axios.CancelToken(c => {
          $api.cancelStack[options.name] = c
        })
      }, options.method === 'delete' ? { transformRequest: null } : {})
      let currentRoute = router && router.currentRoute
      // baseUrl
      if (currentRoute && hosts) {
        let targetHost = hosts.find(host => {
          return host.routeKeys.find(routeKey => routeKey === currentRoute.name)
        })
        options.baseURL = (targetHost || {}).url || null
      }
      return new Promise((resolve, reject) => {
        axios(options).then(resp => {
          resolve(resp)
        }).catch(err => {
          reject(err)
        })
      })
    }
    // 
    const batchAjax = requestArray => {
      return new Promise((resolve, reject) => {
        axios.all(requestArray.map(request => request())).then((resp) => {
          resolve(resp)
        }).catch(err => {
          reject(err)
        })
      })
    }

    globalAxiosOptions = Object.assign({}, defaultsAxiosOptions, globalAxiosOptions)

    const request = options => {
      return Array.isArray(options) ? batchAjax(options) : (() => {
        options = Object.assign({}, globalAxiosOptions, options)
        return ajax(options)
      })()
    }
    const requestWithAliases = (options, method = {}) => {
      options = Object.assign({}, globalAxiosOptions, options, method)
      return ajax(options)
    }
    // 
    const registerMethod = (apiConfig) => {
      apiConfig.forEach(methodConfig => {
        if (!methodConfig.method) {
          console.warn(`%c url: ${methodConfig.url}method`, 'font-size:2em')
          return false
        }
        if ($api[methodConfig.method]) {
          console.warn(`%c (method: ${methodConfig.method})`, 'font-size:2em')
          return false
        }
        $api[methodConfig.method] = (options) => {
          let { url, data, params, type } = methodConfig
          options = Object.assign({}, { method: type || 'get', url, data, params }, options)
          return $api(options)
        }
      })
    }

    let $api = options => {
      return request(options)
    }
    apiConfig && registerMethod(apiConfig)
    $api.cancelStack = {}
    $api.cancel = (name, message) => {
      if (name && !$api.cancelStack[name]) {
        return
      }
      name ? $api.cancelStack[name](message) : Object.values($api.cancelStack).map(c => c(message))
    }
    // 
    $api.request = options => {
      return request(options)
    }
    $api.get = options => {
      return requestWithAliases(options, { method: 'get' })
    }
    $api.delete = options => {
      return requestWithAliases(options, { method: 'delete' })
    }
    $api.post = options => {
      return requestWithAliases(options, { method: 'post' })
    }
    $api.put = options => {
      return requestWithAliases(options, { method: 'put' })
    }
    $api.postFile = options => {
      return requestWithAliases(options, { method: 'post', headers: {'Content-Type': 'multipart/form-data'} })
    }
    $api.all = requestArray => {
      return batchAjax(requestArray)
    }
    // 
    Vue.$api = $api
    // 
    Vue.prototype.$api = $api
  }
}
export default api
